import { Fragment } from 'react'
import PropTypes from 'prop-types'
import NavDrawer, {
  DrawerHeader,
  DrawerSubtitle,
  DrawerTitle,
  DrawerContent,
} from '@material/react-drawer'
import DrawerList from './DrawerList'

const Contents = ({ isMobile, setIsDrawerOpen }) => (
  <Fragment>
    <DrawerHeader>
      <DrawerTitle>
        {isMobile ? 'Mobile Drawer Title' : 'Mobile Desktop Title'}
      </DrawerTitle>
      <DrawerSubtitle>
        {isMobile ? 'Mobile Drawer Subtitle' : 'Mobile Desktop Subtitle'}
      </DrawerSubtitle>
    </DrawerHeader>
    <DrawerContent>
      <DrawerList
        onItemClick={() => setIsDrawerOpen(false)}
      />
    </DrawerContent>
  </Fragment>
)
Contents.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

const Drawer = ({ isMobile, isOpen, setIsDrawerOpen }) => (
  true ? (
    <NavDrawer
      className='app-drawer-mobile'
      modal
      open={isOpen}
      onOpen={() => setIsDrawerOpen(true)}
      onClose={() => setIsDrawerOpen(false)}
    >
      <Contents
        isMobile={isMobile}
        setIsDrawerOpen={setIsDrawerOpen}
      />
    </NavDrawer>
  ) : (
    <NavDrawer
      className='app-drawer-desktop'
    >
      <Contents
        isMobile={isMobile}
        setIsDrawerOpen={setIsDrawerOpen}
      />
    </NavDrawer>
  )
)
Drawer.propTypes = {
  isMobile: PropTypes.bool.isRequired,
  isOpen: PropTypes.bool.isRequired,
  setIsDrawerOpen: PropTypes.func.isRequired,
}

export default Drawer
